import { breakpoint_xs_maxval, css, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const snackbarStyles: GenerateStyles = () => {
    return [
        style,
        css`
            :host {
                --snackbar-margins: 24px;
                --snackbar-opacity: 1;
            }

            :host,
            :host([position='bottom left']) {
                width: fit-content;
                position: fixed;
                bottom: var(--snackbar-margins);
                left: var(--snackbar-margins);
                opacity: var(--snackbar-opacity);
            }

            :host([position='top left']) {
                bottom: unset;
                top: var(--snackbar-margins);
                left: var(--snackbar-margins);
                transform: none;
            }

            :host([position='top center']) {
                bottom: unset;
                top: var(--snackbar-margins);
                left: 50%;
                transform: translateX(-50%);
            }

            :host([position='top right']) {
                bottom: unset;
                left: unset;
                top: var(--snackbar-margins);
                right: var(--snackbar-margins);
            }

            :host([position='bottom center']) {
                left: 50%;
                transform: translateX(-50%);
            }

            :host([position='bottom right']) {
                left: unset;
                right: var(--snackbar-margins);
            }

            :host([position='middle left']) {
                bottom: unset;
                top: 50%;
                left: var(--snackbar-margins);
                transform: translateY(-50%);
            }

            :host([position='middle center']) {
                bottom: unset;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
            }

            :host([position='middle right']) {
                bottom: unset;
                top: 50%;
                left: unset;
                right: var(--snackbar-margins);
                transform: translateY(-50%);
            }

            :host([position='top right']) *,
            :host([position='middle right']) *,
            :host([position='bottom right']) * {
                margin-left: auto;
                margin-right: 0;
            }

            :host([position='top center']) *,
            :host([position='middle center']) *,
            :host([position='bottom center']) * {
                margin-left: auto;
                margin-right: auto;
            }

            @media screen and (max-width: ${breakpoint_xs_maxval}px) {
                :host {
                    width: calc(100% - 16px);
                    --snackbar-margins: 8px;
                }
            }
        `,
    ];
};
