// accepted values
import {
    SnackbarAnimation,
    SnackbarSize,
    SnackbarType,
    SnackbarVariant,
} from '@umetrics/sartorius-ui-snackbar-service';

const acceptedAnimationNames = Object.keys(SnackbarAnimation).map((t) => SnackbarAnimation[t]);
const acceptedSizes = Object.keys(SnackbarSize).map((t) => SnackbarSize[t]);
const acceptedTypes = Object.keys(SnackbarType).map((t) => SnackbarType[t]);
const acceptedVariants = Object.keys(SnackbarVariant).map((t) => SnackbarVariant[t]);

/**
 * Validation schema for the snackbar custom options
 */
export const optionsSchema = {
    accessibilityLabel: (value) => !value || typeof value === 'string',
    animationDuration: (value) => !value || typeof value === 'number',
    animationName: (value) => !value || (typeof value === 'string' && acceptedAnimationNames.includes(value)),
    buttonCallback: (value) => !value || typeof value === 'function',
    buttonCross: (value) => !value || typeof value === 'boolean',
    buttonText: (value) => !value || typeof value === 'string',
    customClasses: (value) => !value || typeof value === 'string',
    duration: (value) => !value || (typeof value === 'number' && !Number.isNaN(value)),
    hideCallback: (value) => !value || typeof value === 'function',
    message: (value) => !value || (typeof value === 'string' && value.length > 0),
    showCallback: (value) => !value || typeof value === 'function',
    size: (value) => !value || (typeof value === 'string' && acceptedSizes.includes(value)),
    type: (value) => !value || (typeof value === 'string' && acceptedTypes.includes(value)),
    variant: (value) => !value || (typeof value === 'string' && acceptedVariants.includes(value)),
    id: (value) => !value || typeof value === 'string',
};
