import {
    breakpoint_xs_maxval,
    css,
    cssSelector,
    cssVariableAsVar,
    GenerateStyles,
    style,
} from '@umetrics/sartorius-ui-base';

export const snackbarMessageStyles: GenerateStyles = (tagName) => {
    const { root, child } = cssSelector(tagName);

    return [
        style,
        css`
            :host {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                width: 360px;
                background-color: ${cssVariableAsVar('color_primary_3')};

                margin-top: 8px;
                border-radius: 0;
                box-shadow: none;
            }

            :host([variant='success']) {
                background-color: ${cssVariableAsVar('color_traffic_light_3')};
            }

            :host([variant='error']) {
                background-color: ${cssVariableAsVar('color_traffic_light_1')};
            }

            :host([variant='warning']) {
                background-color: ${cssVariableAsVar('color_primary_1')};
            }

            :host([variant='info']) {
                background-color: ${cssVariableAsVar('color_tertiary_5')};
            }

            :host([size='small']) {
                padding: calc(1rem / 3) calc(1rem / 2);
            }

            :host([size='small']) ${child('message')} {
                font-size: 0.75rem;
            }

            :host([size='small']) ${child('button')} {
                font-size: 0.75rem;
            }

            :host([size='large']) {
                padding: 0.5rem 1.25rem;
            }

            :host([size='large']) ${child('message')} {
                font-size: 1.5rem;
            }

            :host([size='large']) ${child('button')} {
                font-size: 1.5rem;
            }

            ${root} {
                width: 100%;
            }

            ${child('inner')} {
                display: flex;
                justify-content: space-between;
            }

            ${child('button')} {
                border: none;
                background-color: rgba(0, 0, 0, 0);
                color: ${cssVariableAsVar('color_primary_2')};
                outline: none;
                opacity: 0.75;
                border-radius: 0;
            }

            ${child('close-icon')} {
                font-size: 20px;
            }

            ${child('complement-button')} {
                display: flex;
                align-items: center;
            }

            ${child('cross-button')} {
                display: flex;
                align-items: center;
                border-left: 1px solid rgba(255, 255, 255, 0.5);
            }

            :host([variant='warning']) ${child('cross-button')} {
                --button-text-color: ${cssVariableAsVar('color_primary_3')};
                --button-hover-color: #0000001a;
                --button-hover-text-color: ${cssVariableAsVar('color_primary_3')};
                --button-active-color: #00000033;
                border-left: 1px solid rgba(0, 0, 0, 0.25);
            }

            ${child('text-button')} {
                padding: 2px 8px;
                margin: 0;
                margin-bottom: 4px;
            }

            ${child('message')} {
                color: ${cssVariableAsVar('color_primary_2')};
                margin: 0;
                font-size: 14px;
                display: flex;
                flex: 1 1 auto;
                padding: 8px 10px 8px 12px;
                align-items: center;
            }

            :host([variant='warning']) ${child('message')} {
                color: ${cssVariableAsVar('color_primary_3')};
            }

            ${child('variant-icon')} {
                font-size: 20px;
                margin-right: 8px;
            }

            @media (hover: hover) {
                ${child('button')}:hover {
                    background-color: rgba(0, 0, 0, 0.2);
                    cursor: pointer;
                    opacity: 1;
                }
            }

            ${child('timer')} {
                overflow: hidden;
                background-color: rgba(0, 0, 0, 0.25);
                border-radius: 0;
            }

            ${child('timer')} div {
                height: 4px;
                transform-origin: left center;
                background-color: rgba(255, 255, 255, 0.5);
                animation: roundtime calc(var(--duration) * 1s) linear forwards;
            }

            @keyframes roundtime {
                to {
                    transform: scaleX(0);
                }
            }

            @media screen and (max-width: ${breakpoint_xs_maxval}px) {
                :host {
                    width: 100%;
                }
            }
        `,
    ];
};
