import { SnackbarEvents } from './snackbarEvents';
import { version } from './metadata';

const _dispatchEvent = (name, detail) => {
    let remove;

    window.dispatchEvent(
        new CustomEvent(name, {
            detail: {
                ...detail,
                remove: (fn) => {
                    remove = fn;
                },
            },
        })
    );
    return () => {
        if (remove) {
            remove();
        }
    };
};

/**
 * SnackbarSize
 *
 * (Unused)
 */
export enum SnackbarSize {
    Small = 'small',
    Normal = 'normal',
    Large = 'large',
}

/**
 * SnackbarType
 *
 * Auto : With auto close on duration and visual progressbar
 * Simple : With auto close on duration but no visible progressbar
 * Dismiss : No auto close, need user dismiss
 */
export enum SnackbarType {
    Auto = 'auto',
    Simple = 'simple',
    Dismiss = 'dismiss',
}

/**
 * SnackbarVariant
 *
 * Default : Default style
 * Error : Error styling
 * Warning : Warning styling
 * Info : Info styling
 * Success : Success styling
 */
export enum SnackbarVariant {
    Default = 'default',
    Error = 'error',
    Warning = 'warning',
    Info = 'info',
    Success = 'success',
}

/**
 * SnackbarAnimation
 *
 * Default : Blend in/out opacity
 * SlideIn : Slide in from left
 * Zoom : Zoom in/out effect
 * SlideInTop : Slide in from top
 * SlideInRight : Slide in right
 */
export enum SnackbarAnimation {
    Default = 'default',
    SlideIn = 'slide-in',
    Zoom = 'zoom',
    SlideInTop = 'slide-in-top',
    SlideInRight = 'slide-in-right',
}

/**
 * SnackbarOptions
 *
 * id : Unique ID of message. Could be set if message should later be updated or removed.
 * message : The message string
 *
 * buttonCross : Add a dismiss button in upper right corner
 * buttonText : Add a bottom centered dismiss button
 *
 * accessibilityLabel :
 * animationDuration : duration of animate in/out effect
 * animationName : animation effects, See SnackbarAnimation
 *
 * customClasses :
 * duration : Time until auto close (ms)
 * size :
 * type : Behaviour of message, see SnackbarType.
 * variant : Styling of message
 *
 * hideCallback : Callback function called when message is hidden
 * showCallback : Callback function called when message is visible
 * buttonCallback : Callback function called when a close button is clicked
 */
export interface SnackbarOptions {
    id?: string;
    message?: string;

    buttonCross?: boolean;
    buttonText?: string;
    complementButtonText?: string;

    accessibilityLabel?: string;
    animationDuration?: number;
    animationName?: SnackbarAnimation | string;

    customClasses?: string;
    duration?: number;
    size?: SnackbarSize | string;
    type?: SnackbarType | string;
    variant?: SnackbarVariant | string;

    hideCallback?: null | ((id: string) => {});
    showCallback?: null | ((id: string) => {});
    buttonCallback?: null | ((id: string) => {});
    complementButtonCallback?: null | (() => void);
}

export class SnackbarService {
    static version = version;

    // Static members
    static post(options: SnackbarOptions) {
        return _dispatchEvent(SnackbarEvents.Add, {
            ...options,
        });
    }
    static remove(id: string) {
        return _dispatchEvent(SnackbarEvents.Remove, {
            snackbarID: id,
        });
    }

    // Private members
    // Protected members
    // Public members
    // Protected methods
    // Private methods
}
