import { BaseElement, createClassName, defineElement, html, property } from '@umetrics/sartorius-ui-base';
import { version } from './metadata';
import { SnackbarMessageProperties } from './snackbarMessageProperties';
import { snackbarMessageStyles } from './snackbarMessageStyle';
import {
    SnackbarAnimation,
    SnackbarEvents,
    SnackbarType,
    SnackbarVariant,
} from '@umetrics/sartorius-ui-snackbar-service';

import cancel from '@umetrics/sartorius-ui-icon/dist/icons/cancel';
import error from '@umetrics/sartorius-ui-icon/dist/icons/error';
import alert from '@umetrics/sartorius-ui-icon/dist/icons/alert';
import success from '@umetrics/sartorius-ui-icon/dist/icons/success';
import details from '@umetrics/sartorius-ui-icon/dist/icons/details';

@defineElement({
    tagName: 'snackbar-message',
    styles: snackbarMessageStyles,
    version,
    //    sub: [Icon, Button]
})
export class SnackbarMessage extends BaseElement implements SnackbarMessageProperties {
    // Static members
    private static Icon;
    private static Button;
    private static ButtonSlot;
    private static ButtonType;

    private static animations = {
        [SnackbarAnimation.Default]: {
            showAnimation: [{ opacity: '0' }, { opacity: '1' }],
            hideAnimation: [{ opacity: '1' }, { opacity: '0' }],
        },
        [SnackbarAnimation.SlideIn]: {
            showAnimation: [
                {
                    transform: 'translateX(-100%)',
                    opacity: '0',
                },
                {
                    transform: 'translateX(0)',
                    opacity: '1',
                },
            ],
            hideAnimation: [
                {
                    transform: 'translateX(0)',
                    opacity: '1',
                },
                {
                    transform: 'translateX(-100%)',
                    opacity: '0',
                },
            ],
        },
        [SnackbarAnimation.SlideInRight]: {
            showAnimation: [
                {
                    transform: 'translateX(100%)',
                    opacity: '0',
                },
                {
                    transform: 'translateX(0%)',
                    opacity: '1',
                },
            ],
            hideAnimation: [
                {
                    transform: 'translateX(0%)',
                    opacity: '1',
                },
                {
                    transform: 'translateX(100%)',
                    opacity: '0',
                },
            ],
        },
        [SnackbarAnimation.SlideInTop]: {
            showAnimation: [
                {
                    transform: 'translateY(-100%)',
                    opacity: '0',
                },
                {
                    transform: 'translateY(0)',
                    opacity: '1',
                },
            ],
            hideAnimation: [
                {
                    transform: 'translateY(0)',
                    opacity: '1',
                },
                {
                    transform: 'translateY(-100%)',
                    opacity: '0',
                },
            ],
        },
        [SnackbarAnimation.Zoom]: {
            showAnimation: [
                {
                    transform: 'scale(0)',
                    opacity: '0',
                },
                {
                    transform: 'scale(1)',
                    opacity: '1',
                },
            ],
            hideAnimation: [
                {
                    transform: 'scale(1)',
                    opacity: '1',
                },
                {
                    transform: 'scale(0)',
                    opacity: '0',
                },
            ],
        },
    };

    // Private members

    // Protected members

    // Public members
    @property({ type: String, reflect: false })
    type;
    @property({ type: String, reflect: true })
    variant;
    @property({ type: String, reflect: false })
    accessibilityLabel;
    @property({ type: String, reflect: true })
    message;
    @property({ type: Number, reflect: true })
    duration;
    @property({ type: Boolean, reflect: true })
    animationDone;
    @property({ type: Number, reflect: true })
    animationDuration;
    @property({ type: String, reflect: true })
    animationName;
    @property({ type: Function, reflect: false })
    showCallback;
    @property({ type: Function, reflect: false })
    hideCallback;
    @property({ type: Function, reflect: false })
    buttonCallback;
    @property({ type: String, reflect: true })
    buttonText;
    @property({ type: Boolean, reflect: true })
    buttonCross;
    @property({ type: String, reflect: true })
    complementButtonText;
    @property({ type: Function, reflect: false })
    complementButtonCallback;

    private readonly _showStart;

    constructor() {
        super();

        this._timer = this._timer.bind(this);
        this._showStart = Date.now();
    }

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] });
        imports.push({
            module: () => import('@umetrics/sartorius-ui-button'),
            elem: ['Button', 'ButtonSlot', 'ButtonType'],
        });

        SnackbarMessage.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
            // Start the timer
            if (this.type !== SnackbarType.Dismiss) {
                this._timer();
                this.style.setProperty('--duration', '' + this.duration / 1000);
            }

            // a11y
            this.setAttribute('aria-label', this.accessibilityLabel || `Snackbar: ${this.message}`);
            this.setAttribute('role', 'alert');
        });
    }

    disconnectedCallback() {
        super.disconnectedCallback();
    }

    protected update(changedProperties) {
        super.update(changedProperties);

        // start fadein on first load
        if (!this.animationDone) this._fadeIn();
    }

    protected firstUpdated(changedProperties) {
        super.firstUpdated(changedProperties);
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            [this.type]: true, // simple, auto, dismiss...
        });

        const crossButtonHtml = this.buttonCross
            ? html`
                              <${SnackbarMessage.Button.tag} class="${baseClassName}-cross-button" compact="true" type="${SnackbarMessage.ButtonType.TertiaryOnBlack}" @click="${this._handleButtonClick}">
                                  <${SnackbarMessage.Icon.tag} 
                                      slot='${SnackbarMessage.ButtonSlot.ICON}'
                                      class="${baseClassName}-close-icon" 
                                      icon="${cancel}"></${SnackbarMessage.Icon.tag}>
                              </${SnackbarMessage.Button.tag}>
                          `
            : '';

        const complementButtonHtml = this.complementButtonText
            ? html`
                <${SnackbarMessage.Button.tag} 
                    class="${baseClassName}-complement-button" 
                    type="${SnackbarMessage.ButtonType.TertiaryOnBlack}"
                    @click="${this._handleComplementButtonClick}"
                >
                    ${this.complementButtonText}
                </${SnackbarMessage.Button.tag}>
            `
            : '';

        const textButtonHtml = this.buttonText
            ? html`
                  <button
                      class="${baseClassName}-button ${baseClassName}-text-button"
                      @click="${this._handleButtonClick}"
                  >
                      ${this.buttonText}
                  </button>
              `
            : '';

        const timerHtml =
            this.type === SnackbarType.Auto
                ? html`
                      <div class="${baseClassName}-timer">
                          <div></div>
                      </div>
                  `
                : html``;

        return html`
            <div class="${rootClasses}">
                <div class="${baseClassName}-inner">
                    <p class="${baseClassName}-message">${this._variantIcon(baseClassName)} ${this.message}</p>
                    ${complementButtonHtml} ${crossButtonHtml}
                </div>
                ${textButtonHtml} ${timerHtml}
            </div>
        `;
    }

    private _variantIcon(baseClassName) {
        let icon;
        switch (this.variant) {
            case SnackbarVariant.Error:
                icon = error;
                break;
            case SnackbarVariant.Warning:
                icon = alert;
                break;
            case SnackbarVariant.Success:
                icon = success;
                break;
            case SnackbarVariant.Info:
                icon = details;
                break;
        }
        return icon
            ? html`
                <${SnackbarMessage.Icon.tag}
                    class="${baseClassName}-variant-icon"
                    size="large"
                    icon="${icon}"></${SnackbarMessage.Icon.tag}>
            `
            : html``;
    }

    private _timer() {
        const rightNow = Date.now();
        if (rightNow - this._showStart >= this.duration) {
            this._fadeOut();
            cancelAnimationFrame(this._timer['id']);
        } else {
            this._timer['id'] = requestAnimationFrame(this._timer);
        }
    }

    /**
     * Add fade in animation, dispatch event
     *
     * @private
     */
    private _fadeIn() {
        const animation = this.animationName ? this.animationName : SnackbarAnimation.Default;
        const animationKeyframes = SnackbarMessage.animations[animation].showAnimation;
        const animationOptions = {
            duration: this.animationDuration,
            iterations: 1,
        };
        const fadeInAnimation = this.animate ? this.animate(animationKeyframes, animationOptions) : undefined;

        const doneHandler = () => {
            this.dispatchEvent(
                new CustomEvent(SnackbarEvents.Visible, {
                    bubbles: true,
                    composed: true,
                    detail: {
                        snackbarID: this.id,
                    },
                })
            );

            if (typeof this.showCallback === 'function') {
                this.showCallback(this.id);
            }
        };
        if (fadeInAnimation) {
            fadeInAnimation.onfinish = () => doneHandler();
        } else {
            doneHandler();
        }
    }

    /**
     * Add fade out animation, remove snackbar
     *
     * @private
     */
    private _fadeOut() {
        const animation = this.animationName ? this.animationName : SnackbarAnimation.Default;
        const fadeOutKeyframes = SnackbarMessage.animations[animation].hideAnimation;
        const fadeOutOptions = { duration: this.animationDuration, iterations: 1 };
        const fadeOutAnimation = this.animate ? this.animate(fadeOutKeyframes, fadeOutOptions) : undefined;

        const doneHandler = () => {
            this.dispatchEvent(
                new CustomEvent(SnackbarEvents.Hidden, {
                    bubbles: true,
                    composed: true,
                    detail: {
                        snackbarID: this.id,
                    },
                })
            );
            if (typeof this.hideCallback === 'function') {
                this.hideCallback(this.id);
            }
            this._removeSnackbar();
        };

        // after fadeout animation finishes, remove snackbar
        if (fadeOutAnimation) {
            fadeOutAnimation.onfinish = () => doneHandler();
        } else {
            doneHandler();
        }
    }

    /**
     * Dispatch event to remove snackbar from DOM
     *
     * @private
     */
    private _removeSnackbar() {
        this.dispatchEvent(
            new CustomEvent(SnackbarEvents.Remove, {
                bubbles: true,
                composed: true,
                detail: {
                    snackbarID: this.id,
                },
            })
        );
    }

    /**
     * Handle button click
     *  - trigger dismiss (fade out)
     *  - trigger custom callback function
     *
     * @private
     */
    private _handleButtonClick() {
        this._fadeOut();
        if (typeof this.buttonCallback === 'function') {
            this.buttonCallback(this.id);
        }
    }

    private _handleComplementButtonClick() {
        if (this.complementButtonText) {
            this._fadeOut();
            if (typeof this.complementButtonCallback === 'function') {
                this.complementButtonCallback();
            }
        }
    }
}
