import { BaseElement, defineElement, html, property } from '@umetrics/sartorius-ui-base';
import { snackbarStyles } from './snackbarStyle';
import { SnackbarProperties } from './snackbarProperties';
import { version } from './metadata';
import {
    SnackbarAnimation,
    SnackbarEvents,
    SnackbarOptions,
    SnackbarSize,
    SnackbarType,
    SnackbarVariant,
} from '@umetrics/sartorius-ui-snackbar-service';
import { optionsSchema } from './optionsSchema';
import { SnackbarMessage } from './snackbarMessage';

@defineElement({
    tagName: 'snackbar',
    styles: snackbarStyles,
    version,
    //    sub: [SnackbarMessage]
})
export class Snackbar extends BaseElement implements SnackbarProperties {
    // Static members
    private static _defaultOptions: SnackbarOptions = {
        message: 'snack',

        buttonCross: true,
        buttonText: undefined,
        complementButtonText: undefined,
        complementButtonCallback: undefined,

        accessibilityLabel: undefined,
        animationDuration: 500,
        animationName: SnackbarAnimation.SlideInRight,

        customClasses: undefined,
        duration: 6000,
        size: SnackbarSize.Normal,
        type: SnackbarType.Auto,
        variant: SnackbarVariant.Default,

        hideCallback: undefined,
        showCallback: undefined,
        buttonCallback: undefined,
    };
    // Private members

    // Protected members

    // Public members
    @property({ type: String, reflect: true })
    position = 'bottom right';

    constructor() {
        super();

        // Add event listeners
        window.addEventListener(SnackbarEvents.Add, this._handleSnackbarAdd.bind(this));
        window.addEventListener(SnackbarEvents.Remove, this._handleSnackbarRemove.bind(this));
    }

    /**
     * Dispatch a ready event once the snackbar is added to the dom
     */
    connectedCallback() {
        super.connectedCallback();

        Snackbar.subRegister(SnackbarMessage);

        this.dispatchEvent(
            new CustomEvent(SnackbarEvents.Ready, {
                bubbles: true,
                composed: true,
            })
        );

        // a11y
        this.setAttribute('aria-live', 'polite');
    }

    /**
     * Remove event listeners once the snackbar is disconnected
     */
    disconnectedCallback() {
        super.disconnectedCallback();

        window.removeEventListener(SnackbarEvents.Add, this._handleSnackbarAdd.bind(this));
        window.removeEventListener(SnackbarEvents.Remove, this._handleSnackbarRemove.bind(this));
    }

    protected render() {
        return html``;
    }

    /**
     * Handle snackbar add
     *  - create options object
     *  - create new snackbar
     *  - append new snackbar to DOM
     *
     * @param event
     * @private
     */
    private _handleSnackbarAdd(event) {
        const eventOptions: SnackbarOptions = event.detail || {};
        const validationErrors = this._validateSnackbarOptions(eventOptions);

        if (validationErrors.length > 0) {
            for (const { message } of validationErrors) {
                console.error(
                    'There was a problem creating your snackbar. Please check your custom event options.',
                    message
                );
            }
        } else {
            if (!eventOptions.id) {
                eventOptions.id = Date.now().toString();
            }
            const snackbarOptions = {
                ...Snackbar._defaultOptions,
                ...eventOptions,
            };
            if (this.shadowRoot) {
                const elem = this.shadowRoot.getElementById(eventOptions.id);
                if (elem) {
                    elem.remove();
                }

                const newSnackbar = this._createNewSnackbar(snackbarOptions);

                this.shadowRoot.prepend(newSnackbar);

                this.dispatchEvent(
                    new CustomEvent(SnackbarEvents.Added, {
                        bubbles: true,
                        composed: true,
                        detail: {
                            snackbarID: newSnackbar.id,
                        },
                    })
                );
            }
        }
    }

    /**
     * Remove snackbar from DOM.
     * Dispatch event
     *
     * @param event
     * @private
     */
    private _handleSnackbarRemove(event) {
        if (this.shadowRoot) {
            const elem = this.shadowRoot.getElementById(event.detail.snackbarID);
            if (elem) {
                elem.remove();
            }
            this.dispatchEvent(
                new CustomEvent(SnackbarEvents.Removed, {
                    bubbles: true,
                    composed: true,
                    detail: {
                        snackbarID: event.detail.snackbarID,
                    },
                })
            );
        }
    }

    /**
     * Create new snackbar and add correct properties
     *
     * @param snackbarOptions
     * @returns {HTMLElement}
     * @private
     */
    private _createNewSnackbar(snackbarOptions) {
        const newSnackbarProperties = [
            'message',

            'accessibilityLabel',
            'animationDuration',
            'animationName',

            'buttonText',
            'buttonCross',
            'complementButtonText',
            'complementButtonCallback',

            'duration',
            'type',
            'hasFadedIn',
            'showCallback',
            'hideCallback',
            'buttonCallback',
        ];
        const newSnackbarAttributes = ['id', 'size', 'variant'];

        const tag = SnackbarMessage.tag;
        const newSnackbar = document.createElement(tag[Object.keys(tag)[0]]);

        newSnackbarProperties.forEach((propertyName) => {
            newSnackbar[propertyName] = snackbarOptions[propertyName];
        });

        newSnackbarAttributes.forEach((attributeName) => {
            newSnackbar.setAttribute(attributeName, snackbarOptions[attributeName]);
        });

        newSnackbar.className += 'sartorius-ui-snackbar-message'; // This is for testing, to have a static selector of the message
        if (snackbarOptions.customClasses) {
            newSnackbar.className += ` ${snackbarOptions.customClasses}`;
        }

        return newSnackbar;
    }

    private _validateSnackbarOptions(options) {
        return Object.keys(optionsSchema)
            .filter((key) => !optionsSchema[key](options[key]))
            .map((key) => new Error(`${key} is invalid.`));
    }
}
